define("web-app/tests/lint/templates.template.lint-test", [], function () {
  "use strict";

  QUnit.module('TemplateLint');
  QUnit.test('web-app/templates/application.hbs', function (assert) {
    assert.expect(1);
    assert.ok(false, 'web-app/templates/application.hbs should pass TemplateLint.\n\nweb-app/templates/application.hbs\n  3:19  error  you must use double quotes in templates  quotes\n');
  });
  QUnit.test('web-app/templates/components/connect-4.hbs', function (assert) {
    assert.expect(1);
    assert.ok(false, 'web-app/templates/components/connect-4.hbs should pass TemplateLint.\n\nweb-app/templates/components/connect-4.hbs\n  10:4  error  Incorrect indentation for `<button>` beginning at L10:C4. Expected `<button>` to be at an indentation of 2 but was found at 4.  block-indentation\n  12:4  error  Incorrect indentation for `<button>` beginning at L12:C4. Expected `<button>` to be at an indentation of 2 but was found at 4.  block-indentation\n  10:21  error  you must use double quotes in templates  quotes\n  12:21  error  you must use double quotes in templates  quotes\n');
  });
  QUnit.test('web-app/templates/game.hbs', function (assert) {
    assert.expect(1);
    assert.ok(true, 'web-app/templates/game.hbs should pass TemplateLint.\n\n');
  });
});